<template>
  <div class="s-link" :style="linkStyle">
    <a href="#" @click="handleClick">
      <slot></slot>
    </a>
  </div>
</template>

<script>
export default {
  name: "SLink",
  methods: {
    handleClick(e) {
      e.preventDefault()
      this.$router.push(this.to)
    }
  },
  props: {
    to: {
      type: String,
      required: true
    },
    linkStyle: {
      type: Object,
      default() {
        return {}
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.s-link > a {
  color: inherit;
  text-decoration: none;
  transition: color .3s;

  &:hover {
    color: #002fa7;
  }
}
</style>
